//
// Local and global footer
//

.local-footer {
    border-top: 1px solid $gray-ccc;
    font-size: .9rem;

    &-container {
        padding-top: ($spacer * 1);
        padding-bottom: ($spacer * 1);

        p {
            margin: 0;
        }
    }

    @include media-breakpoint-up('md') {
        .footer-data {
            text-align: right;
        }
    }
}

.footer-section-content-column {
    width: 100%;

    @include media-breakpoint-up('md') {
        width: calc(100% / 4 - var(--section-gap));
        color: $white;
    }

    ul {
        @include list-unstyled();

        li {
            color: var(--frame-color);

            a {
                color: inherit;
            }
        }
    }

    .element-header {
        font-weight: 600;
        font-size: 1.2rem;
        letter-spacing: .06rem;
    }
}

@include media-breakpoint-down('md') {
    .footer-section-content .section-row:first-child {
        --section-gap: 0;

    }

    .footer-section-content-column {
        padding-left: 0;
        padding-right: 0;

        .frame-header {
            cursor: pointer;
            margin-bottom: 0;
            color: var(--frame-color);

            &::after {
                content: '';
                display: block;
                background: $gray-500;
                height: 1px;
                width: 100%;
            }
        }

        .element-header {
            padding: ($spacer * 1) ($spacer * 1.25);
        }

        &>ul {
            &.footerlinks {
                margin: 0;
                transition: height .4s cubic-bezier(.02, .01, .47, 1);
                overflow: hidden;
                background-color: $gray-400;

                &:not(.active) {
                    display: none;
                }

                li {
                    padding: 10px 1rem;

                    &+li {
                        border-top: 1px solid $gray-500;
                        padding-top: 10px;
                    }

                    a {
                        color: $black;
                    }
                }
            }
        }
    }

    .sociallinks {
        pointer-events: none;
    }

    .footer-sociallinks {
        background-color: var(--frame-background);
        color: $white;
        padding: ($spacer * 1) ($spacer * 1.25);
    }
}

@include media-breakpoint-up('sm') {
    .footer-sociallinks .sociallinks-list {
        justify-content: flex-start;
    }
}

@include media-breakpoint-up('md') {
    .footer-section-content {
        padding: ($spacer * 1.5) 0 ($spacer * 1.25) 0;

    }

    .footer-section-content-column {
        .element-header {
            font-size: 1.4rem;
        }

        .frame-header {
            pointer-events: none;
            margin-bottom: .5rem;
        }
    }

}
